body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.submit-button,
.return-button {
    width: 200px;
    height: 70px;
    color: white;
    background: #333;
    border: 5px white solid;
    border-radius: 20px;
    font-family: Arial, Helvetica, sans-serif;
    font-size: 25px;
    cursor: pointer;
    margin-left: 43%;
    margin-bottom: 50px;
}

.return-button {
    margin-top: 50px;
}

.submit-button:hover,
.return-button:hover {
    color: palegreen;
    border: 5px palegreen solid;
    transition: all 0.4s ease-out;
}

.button-opacity {
    opacity: 0.6;
    cursor: not-allowed;
}

.button-opacity:hover {
    color: white;
    border: 5px white solid;
}
.emotion-wrapper {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  text-align: center;
}

.emotion {
  padding: 60px;
  position: relative;
  cursor: pointer;
}

.emotions-title {
font-size: 40px;
color: white;
font-family: Arial, Helvetica, sans-serif;
text-align: center;
padding-top: 50px;
}

.opacity {
opacity: 0.6;
}

.emotion .popover {
  background-color: rgba(0, 0, 0, 0.85);
  border-radius: 5px;
  top: 10px;
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.4);
  color: #fff;
  display: none;
  font-family: 'Helvetica',sans-serif;
  left: 9px;
  padding: 7px 10px;
  position: absolute;
  width: 200px;
  z-index: 4;
}
.emotion .popover:before {
  border-top: 7px solid rgba(0, 0, 0, 0.85);
  border-right: 7px solid transparent;
  border-left: 7px solid transparent;
  bottom: -7px;
  content: '';
  display: block;
  left: 50%;
  margin-left: -7px;
  position: absolute;
}
.emotion:hover .popover {
  display: block;
  -webkit-animation: fade-in .3s linear 1, move-up .3s linear 1;
  animation: fade-in .3s linear 1, move-up .3s linear 1;
}

@-webkit-keyframes fade-in {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@-webkit-keyframes move-up {
  from {
    top: 0;
  }
  to {
    top: 10px;
  }
}
.spinner-wrapper {
    background-color: #333;
    opacity: 0.5;
    text-align: center;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    width: 100vw;
    margin: 0;
  }
  
  .hollowLoader {
    width: 3em;
    height: 3em;
    -webkit-animation: loaderAnim 1.25s infinite ease-in-out;
            animation: loaderAnim 1.25s infinite ease-in-out;
    outline: 1px solid transparent;
  }
  .hollowLoader .largeBox {
    height: 3em;
    width: 3em;
    background-color: #ECECEC;
    outline: 1px solid transparent;
    position: fixed;
  }
  .hollowLoader .smallBox {
    height: 3em;
    width: 3em;
    background-color: #333;
    position: fixed;
    z-index: 1;
    outline: 1px solid transparent;
    -webkit-animation: smallBoxAnim 1.25s alternate infinite ease-in-out;
            animation: smallBoxAnim 1.25s alternate infinite ease-in-out;
  }
  
  @-webkit-keyframes smallBoxAnim {
    0% {
      -webkit-transform: scale(0.2);
              transform: scale(0.2);
    }
    100% {
      -webkit-transform: scale(0.75);
              transform: scale(0.75);
    }
  }
  
  @keyframes smallBoxAnim {
    0% {
      -webkit-transform: scale(0.2);
              transform: scale(0.2);
    }
    100% {
      -webkit-transform: scale(0.75);
              transform: scale(0.75);
    }
  }
  @-webkit-keyframes loaderAnim {
    0% {
      -webkit-transform: rotate(0deg);
              transform: rotate(0deg);
    }
    100% {
      -webkit-transform: rotate(90deg);
              transform: rotate(90deg);
    }
  }
  @keyframes loaderAnim {
    0% {
      -webkit-transform: rotate(0deg);
              transform: rotate(0deg);
    }
    100% {
      -webkit-transform: rotate(90deg);
              transform: rotate(90deg);
    }
  }
  
main {
    padding: .5vw;
    font-size: 0;
    display: -ms-flexbox;
    flex-wrap: wrap;
    flex-direction: column;
    flex-flow: row wrap;
    display: flex;
}
main div {
    flex: auto;
    max-width: 400px;
    margin: .5vw;
}
main div img {
    object-fit: cover;
    height: 300px;
    width: 400px;
}
@media screen and (max-width: 400px) {
    main div { margin: 0; }
    main { padding: 0; }
}
